@import url(https://fonts.googleapis.com/css?family=EB+Garamond|Work+Sans:700);
* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    border: 0;
    outline: 0;
    -webkit-font-smoothing: antialiased;
}

html {
    font-size: 10px;
}

body {
    background: #111;
    color: #fff;
    font: 2.6rem/1.55 "Adobe Garamond Pro", "EB Garamond", Garamond, Georgia, "Times New Roman", serif;
}

section {
    padding: 12.5%;
    width: 100%;
}

section:nth-of-type(even) {
    background: #fff;
    color: #111;
}

p {
    margin-bottom: 2.5em;
}

a {
    color: inherit;
    text-decoration: none;
}

h2 {
    font: bold 2.8rem "Work Sans", "Arial Black", Gadget, sans-serif;
    text-transform: uppercase;
    letter-spacing: 5px;
    margin-bottom: 2em;
}

nav {
    mix-blend-mode: difference;
    z-index: 100;
}

nav ul {
    position: fixed;
    top: 60px;
    right: 5px;
    height: 100vh;
    visibility: hidden;
    pointer-events: none;
    list-style: none;
    width: 35px;
}

nav ul li {
    font: bold 1.5rem "Work Sans", "Arial Black", Gadget, sans-serif;
    text-transform: uppercase;
    letter-spacing: 2px;
    padding: 0.75em 0;
    /*-webkit-writing-mode: vertical-lr;*/
    -ms-writing-mode: tb-lr;
    -webkit-writing-mode: vertical-lr;
            writing-mode: vertical-lr;
}

nav.active ul {
    visibility: visible;
    pointer-events: initial;
    -webkit-transition-delay: 0.2s;
            transition-delay: 0.2s;
}

.nav-icon {
    -webkit-appearance: none;
    -moz-appearance: none;
    /*appearance: none;*/
    background: transparent;
    cursor: pointer;
    display: inline-block;
    height: 35px;
    position: fixed;
    top: 15px;
    right: 15px;
    -webkit-transition: background 0.3s;
    transition: background 0.3s;
    width: 35px;
}

.nav-icon span {
    position: absolute;
    top: 15px;
    left: 5px;
    background: #fff;
    display: block;
    height: 3px;
    right: 5px;
    transition: -webkit-transform 0.3s;
    -webkit-transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
}

.nav-icon span:before, .nav-icon span:after {
    width: 100%;
    height: 3px;
    background: #fff;
    content: "";
    display: block;
    left: 0;
    position: absolute;
}

.nav-icon span:before {
    top: -8px;
}

.nav-icon span:after {
    bottom: -8px;
}

.active .nav-icon span {
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg);
}


.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

